.mainContainer{
    padding: 0.5em;
    justify-items: center;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.formContainer{
    text-align: center;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: repeat(5, 20%);
    grid-gap: 0.5em;
}
.full-row{
    grid-column: 1/3;
}
input{
    padding: 7px;
    outline: none;
    background-color: lightblue;
    border-radius: 10px;
    font-size: 25px;
    font-family: "portfolio"
}
textarea{
    padding: 7px;
    outline: none;
    background-color: lightblue;
    border-radius: 10px;
    font-size: 25px;
    resize: none;
    font-family: "portfolio"
}


