  margin: 1rem;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  display: -ms-inline-flexbox;
  display: inline-flex;
  border: 1px solid black;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
}
.button:hover{
