          box-shadow: 0 0 15px #ff0010;
}
p{
  font-size: 1.9rem;
}
a{
  text-decoration: none;
  color: black;
}
.contact-detail{
  margin: 1rem;
  font-size: 1.7rem;
  width: 100%;
  display: inline-block;
}

.contact{
  display: inline-block;
  margin: 0% 5%;  
}

.span-special{
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
  font-size: 2rem;
  display: inline;
}


