@font-face {
    font-family: portfolio;
    src: url(./fonts/ImpactaLL.ttf);
}

body{   
	margin: 0;   
	padding: 0;   
	font-family: portfolio !important;   
	word-spacing:0.3rem;   
	font-size: 1.7rem;   
	background: -webkit-gradient(linear,right top, left top,from(#43cea2),to(#185a9d));   
	background: -webkit-linear-gradient(right,#43cea2,#185a9d);   
	background: -o-linear-gradient(right,#43cea2,#185a9d);   
	background: linear-gradient(to left,#43cea2,#185a9d);
}




